  .lds-roller {
    position: absolute;
    z-index: 5000;
    width: 80px;
    height: 80px;
    margin-left: -40px;
    margin-top: -40px;
  }
  .lds-roller div {
    animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #2600ff;
    margin: -6px 0 0 -6px;
  }
